import React from 'react'

const DashboardHeader = ({id, firstName, lastName}) => {

  return (
    <div className='w-[600px] h-32 bg-white/20 backdrop-blur-3xl rounded-lg shadow-md fixed top-10'>
        <div>
            <h2>{firstName}</h2>
            <h2>{lastName}</h2>
        </div>
        <div>
            <button>Add Todo</button>
            <button>Log Out</button>
        </div>
    </div>
  )
}

export default DashboardHeader