import React from 'react'
import { cookies } from 'next/headers'
import { redirect } from 'next/navigation'
import DashboardHeader from '../../../components/DashboardHeader'

const Dashboard = async () => {

  const authToken = (await cookies()).get('auth-token')?.value;

  const authResponse = await fetch("http://localhost:3100/api/v1/auth/is-logged-in", {
    method: "POST",
    credentials: "include",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${authToken}`
    }
  });

  if (authResponse.status !== 200) {
    return redirect("/auth");
  }

  const userInfoResponse = await fetch("http://localhost:3100/api/v1/auth/get-me", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${authToken}`
    }
  });

  const userInfo = await userInfoResponse.json();
  
  return (
    <div className="w-full min-h-screen bg-gradient-to-tr from-purple-400 via-pink-500 to-red-500 flex justify-center">
      <DashboardHeader id={userInfo.user._id} firstName={userInfo.user.firstName} lastName={userInfo.user.lastName}/>
    </div>
  )
}

export default Dashboard